.pagination {
  &__list {
    margin: 20px 0;
    display: flex;
  }
  &__listItem {
    padding: 10px 15px;
    border: 1px solid #bdbdbd;
    border-radius: 3px;
    margin-right: 15px;
    cursor: pointer;
    &:hover {
      opacity: 0.7;
    }
  }
  &__listItemActive {
    padding: 10px 15px;
    border: 1px solid #bdbdbd;
    border-radius: 3px;
    margin-right: 15px;
    background-color: #bdbdbd;
    cursor: pointer;
    &:hover {
      opacity: 0.7;
    }
  }
}
